import React, { useEffect, useState } from 'react';
import { useAuth } from './AuthContext';
import { useHistory } from 'react-router-dom';
import axios from 'axios';
const backendLink = process.env.REACT_APP_BACKEND_LINK;
const secretLink="sp=r&si=pgrb&sv=2022-11-02&sr=c&sig=%2F7BSOkRZbjtTMLOaNoY1Zr5VxN%2BEGnQAA%2FsM%2BOmAu0c%3D";


function Dashboard() {

    const {user, logout } = useAuth(); // Access the user context
    const history = useHistory();
    const [userDetails, setUserDetails] = useState(null);

    // Redirect to login if the user is not authenticated
    useEffect(() => {
        if (!user) {
            history.push('/login');
        }
    }, [user, history]);

     //logout
    const handleLogout = () => {
        logout(); // Call the logout function from context
        history.push('/login'); // Redirect to login page
    };

    //back to home pg
    const returnHome = () => {
        history.push('/home'); // Redirect to login page
    };

    // get user details
    useEffect(() => {
        // Fetch user details from backend
        const accessToken = localStorage.getItem('accessToken');
        const fetchUserDetails = async () => {
            try {
                const response = await axios.get(`${backendLink}/users/details`, {
                    headers: {
                        Authorization: `Bearer ${accessToken}`,
                    },
                });
                setUserDetails(response.data);
            } catch (error) {
                console.error('Error fetching user details:', error);
                // Handle error, e.g., redirect to login if unauthorized
                if (error.response && error.response.status === 401) {
                    logout();
                    history.push('/login');
                }
            }
        };

        fetchUserDetails();

    }, [history, logout]);
    
    return (
            <>
                <button className="btn btn-secondary mb-3" onClick={handleLogout}>Logout</button>
                <button className="btn btn-secondary mb-3" onClick={returnHome}>Back to home</button>
                <div className="d-flex justify-content-center align-items-center mt-n5">
                    <div className="container bg-danger py-4 px-3 m-4 rounded">
                        <h3 className="text-center text-white">My Profile</h3>
                                {userDetails ? (
                                <div className="text-center text-white">
                                    <p>User ID: {userDetails.userId}</p>
                                    <p>Name: {userDetails.name}</p>
                                    <p>Email: {userDetails.email}</p>
                                    <p>Address: {userDetails.address}</p>
                                    <p>Id Proof:  <a href={`${userDetails.fileLink}?${secretLink}`} className='text-white'>view Here</a></p>
                                </div>
                            ) : (
                                <p className="text-center text-white">Loading user details...</p>
                            )}
                    </div>
                </div>
            </>
)};

export default Dashboard;